<template>
	<div class="sHeader">
		<div class="topArea clearfix">
			<div class="sTopBg">
				欢迎来到天津青年创新创业大赛官方网站
			</div>
			<div class="sLogen">
				<!--<img :src="logo" alt="logo" />-->
			</div>
			
			<div class="header-nav">
				<div id="sName">
					天津青年创业创新官网网站
					<!--<img :src="name" alt="名称" />-->
				</div>
				<ul class="navCates">
					<li v-for='(item,index) in navCates' :class="{active:isActive==index}" @click="classActive($event.target)">
						<router-link v-bind:to='item.link'>{{item.name}}</router-link>
					</li>
				</ul>
			</div>
			
		</div>
	</div>
</template>
<script>
	export default {
		props:['isActive'],
		data() {
			return {
//				logo: require('./logo.png'),
//				name: require('./name.png'),
				navCates: [{
						link: '/survey',
						name: '赛事概况',
					},
					{
						link: '/newsCenter',
						name: '新闻中心',
					},
					{
						link: '/review',
						name: '往届回顾',
					},
					{
						link: '/chuangke',
						name: '津彩创业',
					},
					{
						link: '/tjcyzc',
						name: '天津创业政策',
					},
					{
						link: '/chuangke',
						name: '天津创客空间',
					},
					{
						link: '/video',
						name: '视频直播',
					}
				],
			}
		},
		methods: {
			classActive(_target){
				$(_target).parent('li').addClass('active').siblings().removeClass('active');
			}
		},
		mounted: function() {

		}
	}
</script>
<style>
	.sHeader {
		height: 440px;
		width: 100%;
		position: relative;
		z-index:9999;
		background: url("./topbg.jpg") no-repeat top center;
		background-size: 100% 100%;
		overflow: hidden;
	}
	
	.sTopBg {
		text-align: center;
		background: #FFFFFF;
		height: 30px;
		line-height: 30px;
	}
	.header-nav{
		width: 1200px;
		position: relative;
		margin: 0 auto;
		margin-top: 10px;
		 font-size: 15px;
    font-family: "Microsoft YaHei";
        background: #fff;
    overflow: hidden;
	}
	#sName {
		float: left;
	    height: 63px;
	    line-height: 63px;
	}
	.navCates {
		float: right;
		overflow: hidden;
	}
	
	.navCates li {
		float: left;
		width: 140px;
		height: 63px;
		text-align: center;
		color: #fff;
		font-size: 1.4rem;
		/*background: url('./button_normal.png') no-repeat;
		background-size: 100% 100%;*/
		transition: background 0.3s;
		-moz-transition: background 0.3s;
		-o-transition: background 0.3s;
		-webkit-transition: background 0.3s;
	}
	
	.navCates li:hover {
		background: #19ADC7;
     color: #FFFFFF;
	}
	
	.navCates li.active {
		background: #19ADC7;
     color: #FFFFFF;
	}
	
	.navCates li a {
		display: inline-block;
		width: 100%;
		height: 100%;
		line-height: 63px;
		text-align: center;
		color: #000000;
		/*font-size: 1.4rem;*/
		/*text-shadow: 0 0 10px #009eff;*/
	}
	
</style>


