<template>
	<div class="screenWrapper">
		<s-header :isActive='isActive'></s-header>
		<div class="screenContent">
			<transition name="move" mode="out-in">
				<router-view></router-view>
			</transition>
		</div>
		<s-footer></s-footer>
	</div>
</template>
<script>
	import sHeader from '../../common/sHeader/sHeader.vue';
	import sFooter from '../../common/sFooter/sFooter.vue';
	var common_tools = require("common_tools");
//	require("../../../public_resource/vendor/jquery.actual.min.js")
//	require("../../../public_resource/vendor/world.js")
	export default {
		components:{
			sHeader,
			sFooter
		},
		data() {
			return {
				isActive:'1'
			}
		},

		methods: {
			
		},
		mounted: function() {
			if(this.$route.path==='/newsCenter'){
				this.isActive='0';
			}else if(this.$route.path==='/survey'){
				this.isActive='1';
			}else if(this.$route.path==='/review'){
				this.isActive='2';
			}else if(this.$route.path==='/sAssets'){
				this.isActive='3';
			}else if(this.$route.path==='/tjcyzc'){
				this.isActive='4';
			}else if(this.$route.path==='/chuangke'){
				this.isActive='5';
			}else if(this.$route.path==='/video'){
				this.isActive='6';
			}
		},
	}
</script>
<style>
	.screenWrapper{
		/*height:100%;*/
		overflow:hidden;
		background: #FFFFFF;
	
	}
	
	.screenContent{
		/*position:absolute;*/
		width:100%;
		height: auto;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		/*background:url('frame.png') no-repeat;*/
		/*background-size:100% 100%;*/
	}
</style>


